
.slick-slider {
  margin-bottom: 0px;
}

.slick-dots {
  position: absolute;
  bottom: 5px;
  display: block;
  width: 100%;
  padding: 0;
  list-style: none;
  text-align: center;
}

.slick-dots li {
  position: relative;
  display: inline-block;
  width: 20px;
  height: 20px;
  margin: 0 5px;
  padding: 0;
  cursor: pointer;
}

.slick-dots li button {
  font-size: 0;
  line-height: 0;
  display: block;
  width: 20px;
  height: 20px;
  padding: 5px;
  cursor: pointer;
  color: transparent;
  border: 0;
  outline: none;
  background: transparent;
}

.slick-dots li button:hover,
.slick-dots li button:focus {
  outline: none;
}

.slick-dots li button:hover:before,
.slick-dots li button:focus:before {
  opacity: 1;
}

.slick-dots li button:before {
  font-family: 'slick';
  font-size: 26px;
  line-height: 20px;
  position: absolute;
  top: 0;
  left: 0;
  width: 20px;
  height: 20px;
  content: '•';
  text-align: center;
  opacity: .25;
  color: black;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.slick-dots li.slick-active button:before {
  opacity: .75;
  color: black;
}

.slick-prev,
.slick-next {
  font-size: 0;
  line-height: 0;
  position: absolute;
  top: 50%;
  display: block;
  width: 50px;
  height: 50px;
  margin-top: -10px;
  padding: 0;
  cursor: pointer;
  color: transparent;
  border: none;
  outline: none;
  background: rgba(0, 0, 0, 0.61);
  border-radius: 50px;
  z-index: 10;
}

.slick-prev:hover,
.slick-prev:focus,
.slick-next:hover,
.slick-next:focus {
  color: transparent;
  outline: none;
  background: rgba(0, 0, 0, 0.61);
}

.slick-prev:hover:before,
.slick-prev:focus:before,
.slick-next:hover:before,
.slick-next:focus:before {
  opacity: 1;
}

.slick-prev.slick-disabled:before,
.slick-next.slick-disabled:before {
  opacity: .25;
}

.slick-prev:before,
.slick-next:before {
  font-family: 'slick';
  font-size: 20px;
  line-height: 1;
  opacity: .75;
  color: white;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.slick-prev {
  left: 5px;
}

[dir='rtl'] .slick-prev {
  right: -25px;
  left: auto;
}

.slick-prev:before {
  content: '←';
}

[dir='rtl'] .slick-prev:before {
  content: '→';
}

.slick-next {
  right: 5px;
}

[dir='rtl'] .slick-next {
  right: auto;
  left: -25px;
}

.slick-next:before {
  content: '→';
}

[dir='rtl'] .slick-next:before {
  content: '←';
}


body{
  font-family: 'Georgia';
}
.new-2019{
  .container{
    //width: 1300px;
    //border: 2px solid;
  }

  .nav{
    background-color: rgb(212, 199, 157);
    padding: 26px;
    width: 100%;
    margin: auto;
    .buttons{
      float:right;
      position:relative;
      left:-50%;
      text-align:left;
    }
    ul{
      padding: 0;
      margin: 0;
      list-style:none;
      position:relative;
      left:50%;
      li{
        float:left;
        position:relative;
        display: inline-block;
        font-size: 18px;
        font-weight: 300;
        text-transform: uppercase;
        text-align: center;
        padding: 0 15px;
        a{
          color: #422a20;
          &:hover{
            color: #0a0a0a;
          }

        }
      }
    }
  }

  .header{
    background: url("/css/images/new2019/russdoors-header.jpg") center center;
    width: 100%;
    height: 240px;
    &__address{
      color: #b38a54;
      font-size: 23px;
      line-height: 1.4;
      font-weight: 400;
      text-align: right;
      padding: 43px 50px 0 0;
      p{
        margin-bottom: 0;
      }
    }
    img{
      width: 240px;
      position: relative;
      left: 35px;
      top: 5px;
    }
  }

  .home-slider{
    overflow: hidden;
    .slide{
      margin: -135px 0;
      img{
        width: 100%;
      }
    }
  }

  .bestseller{
    h1{
      color: rgb(66, 42, 32);
      font-size: 52px;
      font-weight: bold;
      margin: 20px 0;
    }

    h2{
      color: rgb(66, 42, 32);
      font-size: 30px;
      font-weight: bold;
      text-align: center;
      margin: 40px 0;
    }

    .bg-s{
      background: #f2ede6;
      width: 100%;
      padding: 40px 0;
    }
    .doors-list{
      .door-item{
        img{
          width: 100%;
        }
      }
    }
  }

  .download-catalog{
    height: 80vh;
    position: relative;
    background: url("/css/images/new2019/russdoors-bg1.jpg") no-repeat 100%;
    width: 100%;
    background-size: cover;
    .container{
      position: relative;
      height: 80vh;
    }
    .block{
      margin: 0 50px;
      padding: 60px 45px 70px;
      background-color: #ede2cc;
      width: 90%;
      position: absolute;
      top: 50%;
      transform: translateY(-50%);
      h3{
        font-size: 42px;
        color: #000;
        font-family: 'Roboto',Arial,sans-serif;
        font-weight: bold;
        text-align: center;
      }
      p{
        font-size: 24px;
        color: #0a0a0a;
        margin-top: 24px;
        font-family: 'Roboto',Arial,sans-serif;
        text-align: center;
      }
      &__width{
        max-width: 600px;
        margin: auto;
        text-align: center;
        padding-top: 50px;
        input{
          width: 100%;
          color: #000000;
          border: 2px solid #cfcfcf;
          background-color: #ffffff;
          border-radius: 1px;
          padding: 14px;
        }
        .btn{
          color: #ffffff;
          background-color: #b58f6b;
          border-radius: 1px;
          box-shadow: 0px 0px 10px 0px rgba(0, 0, 0, 0.6);
          border: 0;
          padding: 14px;
          width: 100%;
        }
      }
    }
  }

  .quality{
    background: url("/css/images/new2019/russdoors-bg.jpg") no-repeat;
    background-size: cover;
    width: 100%;
    padding: 40px;
    h2{
      color: #ede2cc;
      font-size: 42px;
      font-family: 'Arial';
      line-height: 1.55;
      font-weight: 700;
      background-position: center center;
      border-color: transparent;
      border-style: solid;
      text-align: center;
    }

    .row{
      margin: 0 100px 0 200px;
    }

    &-item{
      margin-bottom: 50px;
      position: relative;
      padding-right: 70px;
      &:before{
        content: '';
        position: absolute;
        left: -40px;
        top: 30px;
        background: url(/css/images/new2019/done.svg) no-repeat;
        background-size: 45px;
        width: 100%;
        height: 200px;
      }
      h3{
        color: #ede2cc;
        font-size: 22px;
        font-family: 'Arial';
        line-height: 1.55;
        font-weight: 700;
        background-position: center center;
        border-color: transparent;
        border-style: solid;
      }
      p{
        color: #ede2cc;
        font-size: 16px;
        font-family: 'Arial';
        line-height: 1.55;
        font-weight: 400;
        background-position: center center;
        border-color: transparent;
        border-style: solid;
      }
    }
  }

  .about-us{
    h2{
      text-align: center;
      font-size: 38px;
      font-weight: bold;
      margin: 40px 0 70px;
      color: rgb(66, 42, 32);
    }
    p{
      font-size: 20px;
      font-weight: 300;
      font-family: 'Roboto',Arial,sans-serif;
      margin-bottom: 40px;
    }
    .inner-c{
      padding: 0 250px;
    }
    h3{
      text-align: center;
      font-size: 26px;
      color: rgb(66, 42, 32);
      font-weight: bold;
    }
    table{
      border-spacing: 10px;
      border-collapse: initial;
      width: 1000px;
      tr{
        td{
          img{
            max-height: 160px;
          }
        }
      }
    }
  }

  .reviews{
    background-color: #b58f6b;
    padding: 60px 0;
    h2{
      text-align: center;
      font-size: 42px;
      font-weight: bold;
      font-family: 'Roboto',Arial,sans-serif;
      margin-bottom: 50px;
    }
    .review-slider{
      .review-item{
        width: 1000px;
        margin: auto;
        background: white;
        height: 360px;
        p{
          padding: 40px;
          font-size: 20px;
          font-family: 'Roboto',Arial,sans-serif;
        }
        img{
          width: 100%;
          max-height: 360px;
        }
      }
    }
  }

  .feedback{
    padding: 30px 0;
    background-color: #ede2cc;
    h2{
      text-align: center;
      font-size: 42px;
      font-weight: bold;
      margin-bottom: 50px;
      font-family: 'Roboto',Arial,sans-serif;
