      font-size: 30px;
      font-weight: bold;
    }
    #map{
      height: 500px;
      width: 100%;
    }
    .map-content{
      position: absolute;
      width: 550px;
      left: 50%;
      transform: translateX(-400px);
      top: 50px;
      background-color: #ede2cc;
      padding: 40px;
      z-index: 100;
      .social-links{
        padding: 0;
        margin: 0;
        li{
          display: inline-block;
          margin-right: 5px;
          a{
