  .footer{
    position: relative;
    background: #422a20;
    padding: 50px;
    margin-top: 10px;
    .buttons{
      float:right;
      position:relative;
      left:-50%;
      text-align:left;
    }
    &__menu{
      padding: 0;
      margin: 0;
      list-style:none;
      position:relative;
      left:50%;
      li{
        float:left;
        position:relative;
        display: inline-block;
        font-size: 14px;
        font-weight: 300;
        text-transform: uppercase;
        text-align: center;
        padding: 0 15px;
        a{
          color: #b58f6b;
          &:hover{
