    &__logo{
      img{
        display: block;
        margin: 90px auto 30px;
        width: 150px;
      }
    }
    &__copyright{
      text-align: center;
      color: #b58f6b;
    }
  }

  .padding-30{
    padding: 0 30px;
  }
